@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&display=swap);
:root {
  --text-color: #f0f0f0;
  --text-color-secondary: #b3c5cd;

  --primary-color: #21b6b7;
 

  --background-color: #252329;
  --background-color-light: #3c393f;
  --background-color-dark: #120f13;

  --box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.05);

  --font-family: 'Josefin Sans', sans-serif;


}
body {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--font-family);
  max-width: 75vw;
margin: 0 auto;
padding: 0 0.5rem;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
background-color: #252329;
background-color: var( --background-color);
color: #f0f0f0;
color: var(--text-color);
}

 h1{
  font-size: 2em;
 }
 h2{
  font-size: 1.5em;
  color: var(-text-color-secondary);
 }
 h3{
  font-size: 1.3em;
  color: var(-text-color-secondary);
 }
 h4{
  font-size: 1em;
  color: var(-text-color-secondary);
 }
 h5{
  font-size: .8em;
  color: var(-text-color-secondary);
 }
 h6{
  font-size: .7em;
  color: var(-text-color-secondary);
 }

 p{
   color: var(-text-color-secondary);
 }



  
  .bitcoin{
    padding-top:80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color:#888888;}
  
  .path {
    stroke-dasharray: 2110;
    stroke-dashoffset: 2110;
    stroke-width:2;
    stroke-linecap:round;
    animation: dash 6s linear infinite;
    fill-opacity:0;
    stroke:orange;
  }
  
  @keyframes dash {
    0% {
      stroke-dashoffset: 2110;
      opacity:0;
      stroke:orange;
    }
    15%{
      opacity:1;
      stroke:orange;
    }
    70%{
      opacity:1;
      stroke:orange;
    }
    100%{
      stroke-dashoffset:0;
      opacity:0;
      stroke:yellow;
    }
  }
